}

// ---------- модалки
// всередині details-modal
.details-modal.light-theme {
  background-color: #fff;
  color: $card-title-color;

  & .votes {
    background: $modal-background-rating-color;
  }

  & .modal-buttons__add-watched,
  .modal-buttons__add-queue,
  .modal-buttons__remove-watched,
  .modal-buttons__remove-queue {
    border-color: $btn-border-color-without-accent-bground;
  }
}

.details-modal.dark-theme {
  background-color: $modal-dark-bgd;
  color: $modal-dark-color;

  & .votes {
    background: #747474;
  }

  & .modal-buttons__add-watched,
  .modal-buttons__add-queue,
  .modal-buttons__remove-watched,
  .modal-buttons__remove-queue {
    color: $modal-dark-color;
    border-color: $modal-btn-dark-border-color;
  }

  & .close-button::before,
  .close-button::after {
    background-color: #e2e2e2;

    transition: background-color $duration $timing-function;
  }
  & .close-button:hover::before,
  .close-button:hover::after,
  .close-button:focus::before,
  .close-button:focus::after {
    background-color: $accent-color;
  }
}

// всередині модалки реєстрації
.registration-modal.light-theme {
  background-color: #fff;
  color: $card-title-color;
}

.registration-modal.dark-theme {
  background-color: $modal-dark-bgd;
  color: $modal-dark-color;
}

// всередині карточки команди
.team-card.light-theme {
  background-color: #fff;

  &:before {
    background-color: #cecece;
  }

  &:after {
    background-color: #eeeeee;
  }
}

.team-card.dark-theme {
  background-color: $modal-dark-bgd;

  &:before {
    background-color: #2f2f2f;
  }

  &:after {
    background-color: #3e3e3e;
  }

  & .close-crew-btn {
    background-color: $modal-dark-bgd;
  }
}

// ---------- footer
footer.light-theme {
  background-color: $footer-background-color;
  color: $footer-text-color;

  & .footer-link-btn {
    color: $footer-text-color;

    &:hover,
    :focus {
      color: $accent-color;
    }
  }
}

footer.dark-theme {
  background-color: $footer-dark-bgd;
  color: $footer-dark-color;

  & .footer-link-btn {
    color: $footer-dark-color;

    &:hover,
    :focus {
      color: $accent-color;
    }
  }
}

